<template>
    <iframe :src="src" ref="iframe" id="iframe" width="100%" height="100%"></iframe>
</template>

<script>
  export default {
    name: 'viewInvoice',
    data() {
      return {
        src: '',
      };
    },
    created() {
        this.src = localStorage.getItem("src")//this.$route.query.src;
    },
    mounted() {
      var iframe = document.getElementById('iframe');//this.$refs.iframe;
      var iwindow = iframe.contentWindow;
      iframe.addEventListener('load',function () {
         let idoc = iwindow.document;
         let payButton = idoc.getElementsByTagName('button')[1];
         payButton.style.color = "#ffffff";
         payButton.style.background = "#2dcc70";
      })
    }
  };
</script>

<style scoped>
    *{
        padding: 0;
        margin: 0;
    }
    iframe{
        height: 100%;
        width: 100%;
        border: 0;
    }
</style>
